import React from 'react'
import { BrowserRouter , Route } from 'react-router-dom'
import Detail1 from './Detail1';
import Detail2 from './Detail2';
import Detail3 from './Detail3';
import Header from './Header';
import orderform from './order';
import OrderDisplay from './OrderDisplay';


const Roughting= ()=>{
    return(
        <BrowserRouter>
 <Header/>
        <div>
           
            <Route exact path='/' component={Detail1}>

            </Route>
            <Route  path='/Detail2/:id' component={Detail2}>

            </Route>
            <Route path='/detail3/:id' component={Detail3}>

            </Route>
            <Route path='/order/:id' component={orderform}>

             </Route>
             <Route path='/OrderDisplay' component={OrderDisplay}>

             </Route>
        </div>
        
        </BrowserRouter>
       
    )
   
}
export default Roughting;