import React ,{Component} from 'react'
import {Link} from 'react-router-dom'
//import QuickSearchDisplay from './QuickSearchDisplay'
import QuickSearchDisplay from './QuickSearchDisplay.css'

class Quicksearch extends Component{
    constructor(){
        super();
        this.state={

            mealtype:''
        }
    }
    listmeal=(data)=>{
        if(data){
            return data.map((item)=>{


                return (
                    <Link to={`/Detail2/${item.mealtype}`}>
                          <div class="tilecontainer">
                  <div class="tileComponent1">
                    <img src={item.imageurl}/>
                   </div>
                   <div class="tileComponent2">
                   <div class="componentHeading">
                       {item.name}
                   </div>
                   <div class="componentSubHeading">
                       {item.Desc}
                   </div>
                   </div>
                
                </div>
                 
                    </Link>
                  
                    
                )
            }) 
        }

    }
    render()
    {
        return(
           /* <div>

               <QuickSearchDisplay mealData= {this.state.mealtype}/>
               <hr/>

            </div>
            */
           <div>
               
      <div >
      
        <p id="quickhead" >Quick Search</p>
        <p id="quickhead2">Discover Restaurants By Type Of Meal</p>
        
           
                 
          
            
                
               
                 
                 
                 
       
               {this.listmeal(this.state.mealtype)}
           </div>
                
            
                
                
   </div>
              

        )
    }
    componentDidMount()
    {
        
    
      fetch('http://localhost:8700/mealType')
      .then((response)=>{
       response.json()
      .then((data) => {
     //   console.log(data);

       this.setState({mealtype:data});
    });
});


    }
}


export default Quicksearch;