import React,{Component} from 'react'
import Detail3cs from './Detail3cs.css'
import { Link } from 'react-router-dom';
import 'react-tabs/style/react-tabs.css';
import{Tab ,Tabs,TabList,TabPanel} from 'react-tabs';
const detailrestourl="http://localhost:8700/restanrantsIds"
class Detail3 extends Component{
    constructor(props)
    {
        super(props)
        this.state={
            restanrants:''
        }
    }
  
    restanrantsdetail=(data)=>{

        if(data)
        {
            return data.map((item)=>{
                return(
                    <div id="detail">
            
            
            <img   className="img-responsive immg" height="70px" width="1100px"    src={item.thumb}   />
            <div className="row">
              <button class="btn  btn-danger btn-lg btnsuccess " onClick={this.backButton}>
                BACK
              </button>
              <Link to={`/order/${this.props.match.params.id}`}>
              <button className="btn btn-success btn-lg btnsuccess">
                Place Order
              </button>
              </Link>
            </div>
<div className="jumbotron text-center">
        <div className="quickSearchContainer">
          <a id="txt3">{item.name}</a>
         
          <br/><br/> 


            <Tabs>
             

                     <TabList>
                            <Tab>  <a id="txt4"><strong><u>Overview</u></strong> </a>   </Tab>
                             <Tab><a id="txt4">  <u><strong> Contact</strong></u></a></Tab>          
                     </TabList>

                     
                     <TabPanel>
                       <div>
                       <div>
                        <a id="sub">Restaurant</a>
                        </div>
                        <br/>
                       <a id="sub">{item.name}</a>
                       <br/>
                       <a id="sub">Cost:{item.cost}</a>
                          <br/>
                          <a id="sub">City:{item.city_name}</a>
                          <br/>
                          </div>
                    </TabPanel>

                    <TabPanel>
                <div>
                      <div>
                         <a id="sub">About us</a>
                         </div>
                         <br/>
                         <a id="sub">{item.locality}</a>
                         <br/>
                        <a id="sub">{item.address}</a>
                         <br/>
                   
                </div>
            </TabPanel>

            </Tabs>






         
        </div>
 </div>   
           </div>
                )
            })
        }
    }
    backButton=(event)=>{
         let mealid1=parseInt(sessionStorage.getItem('mealId',this.props.match.params.id));
            this.props.history.push(`/Detail2/${mealid1}`);
    }
    render(){
       // console.log("this.props");
       // console.log(this.props);

        return(
            <div>
 

 
<nav className="navbar navbar-inverse ">
          <div className="container-fluid">
          <div className="navbar-header">
            <button type="button" className="navbar-toggle" data-toggle="collapse" data-target="#myNavbar">
              <a className="icon-bar"></a>
   <a className="icon-bar"></a>
   <a className="icon-bar"></a> 
        
        </button>
        <a className="navbar-brand"  id="logo">P</a>
        </div>
         <div className="collapse navbar-collapse" id="myNavbar">
         <Link to={`/`}>
                         <ul className="nav navbar-nav">
                           
                             <li><a  id="a1"> HOME</a></li>
                           
                         </ul>
                         </Link>
                         <ul className="nav navbar-nav navbar-right">
                        <li><a href="#" id="ul2"><span className="glyphicon glyphicon-user" id="gf"></span> Create an account</a></li>
                        <li><a href="#" id="ul2"><span className="glyphicon glyphicon-log-in" id="gf"></span> LogIn</a></li>
                        </ul>
                       
                     </div>
       </div>
       
       
       
       </nav>
       <br/><br/><br/>


      

            {this.restanrantsdetail(this.state.restanrants)}
            </div>
        )
    }
    componentDidMount(){


        fetch(`${detailrestourl}/${this.props.match.params.id}`)
        .then((response)=>{
         response.json()
        .then((data) => {
          console.log(data);
        this.setState({restanrants:data})
       // sessionStorage.setItem('mealId',this.props.match.params.id);
        
      });
  });
      }
}
export default Detail3;